.skillBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1.5rem;
  height: 90vh;
}

.skillDescription,
.wideSkillDescription {
  width: 95%;
  font-size: 1.125rem;
  word-spacing: 5px;
  margin-top: 2vh;
}

.icon {
  max-width: 12rem;
  max-height: 12rem;
  width: 26vh;
  height: 26vh;
  margin-bottom: 2vh;
}

.name {
  font-size: 2rem;
}

@media only screen and (max-width: 599px) {
  .skillBox:nth-of-type(odd) {
    background-color: #ffd4c2;
  }
}

@media only screen and (min-width: 600px) {
  .skillBox {
    justify-content: flex-start;
    height: 30rem;
    width: 50%;
    padding: 15vh 0;
  }
  .skillBox:nth-of-type(1),
  .skillBox:nth-of-type(4),
  .skillBox:nth-of-type(5),
  .skillBox:nth-of-type(8) {
    background-color: #ffd4c2;
    height: fit-content;
  }

  .skillDescription {
    width: 75%;
    max-width: 50rem;
  }

  .wideSkillDescription {
    width: 90%;
    max-width: 50rem;
  }

  .name {
    font-size: 2.25rem;
  }
}
